<template>
	<view class="toptabbar">
		<view class="ul">
			<span  v-for="(item,index) in toplist" @click='homeTopTabbar(index)'  :key="index">
				<span :class ='{show:index == show}' >{{item}}</span>
			</span>
		</view>
	</view>
</template>

<script>
	export default{
		name:"toptabbar",
		data(){
			return{
				toplist:['关注','推荐','热榜','直播','视频'],
				show:1
			}
		},
		methods:{
			homeTopTabbar(index){
				this.show = index
			}
		}
	}
</script>

<style lang="scss">
	.toptabbar{
		width: 100%;
		height: 30px;
		line-height: 20px;
		font-size: 16px;
	}
	.show{
		font-size: 18px;
		font-weight: bold;
	}
	.ul{
		span{
			width: 100rpx;
			height: 30px;
			line-height: 30px;
			font-size: 14px;
			float: left;
		}
	}
</style>
